/* =======================================================
   MODO OSCURO DEL SISTEMA - ADAPTACIN AUTOMTICA
   ====================================================== */

/* Cuando el sistema est en modo oscuro, aplicar clase para referencia */
[data-theme="dark"],
html.system-dark-mode,
body.system-dark-mode {
  color-scheme: dark;
}

/* Asegurar que los colores se ajusten automticamente al modo oscuro del sistema */
@media (prefers-color-scheme: dark) {
  :root {
    /* Estos valores pueden ser sobrescritos por los temas, pero sirven como fallback */
    color-scheme: dark;
  }
}

