/* ===========================================
    ESTILO PREMIUM PINA (SIN NEN)
   Elegante, suave, corporativo.
=========================================== */

/* =======================================================
    ESTILOS GLOBALES PARA INPUTS Y SELECTS
   Asegurar que todos sean redondeados y visibles
=========================================== */
input[type="text"],
input[type="number"],
input[type="date"],
input[type="email"],
input[type="password"],
select,
textarea {
  border-radius: var(--radio-full) !important;
  background: var(--fondo-input) !important;
  color: var(--texto-principal) !important;
  border: 1px solid var(--borde-medio) !important;
  padding: 6px 10px !important;
  font-size: 0.9rem !important;
  transition: all 0.2s ease !important;
}

input[type="text"]::placeholder,
input[type="number"]::placeholder,
input[type="date"]::placeholder,
input[type="email"]::placeholder,
input[type="password"]::placeholder,
textarea::placeholder {
  color: var(--texto-terciario) !important;
  opacity: 0.7 !important;
}

select option {
  background: var(--fondo-input) !important;
  color: var(--texto-principal) !important;
}

input[type="text"]:focus,
input[type="number"]:focus,
input[type="date"]:focus,
input[type="email"]:focus,
input[type="password"]:focus,
select:focus,
textarea:focus {
  outline: none !important;
  border-color: var(--azul-primario) !important;
  box-shadow: 0 0 0 3px rgba(74, 158, 255, 0.15) !important;
  background: var(--fondo-input-focus) !important;
  color: var(--texto-principal) !important;
}

.card {
  background: var(--fondo-card);
  border-radius: var(--radio-xl);
  padding: var(--espacio-lg);
  border: 1px solid var(--borde-sutil);
  box-shadow: var(--sombra-xs);
  margin-top: 12px;
  transition: all var(--transicion-media);
  overflow-x: auto;
}

.card:hover {
  box-shadow: var(--sombra-lg);
}

/* =======================================================
    PESTAAS PREMIUM
======================================================= */
.tabs-container {
  display: flex;
  gap: 8px;
  margin-bottom: 16px;
  overflow-x: auto;
  padding-bottom: 4px;
  background: transparent !important;
}

.tab-btn {
  padding: 8px 14px;
  border-radius: var(--radio-full);
  border: 1px solid var(--borde-sutil);
  background: var(--fondo-input);
  cursor: pointer;
  font-weight: 500;
  font-size: 0.9rem;
  white-space: nowrap;
  transition: all var(--transicion-media);
  color: var(--texto-principal);
}

.tab-btn:hover {
  box-shadow: var(--sombra-sm);
  transform: translateY(-1px);
}

.tab-btn:active {
  transform: translateY(0) scale(0.98);
}

.tab-btn.active {
  background: var(--fondo-secundario);
  color: var(--texto-principal);
  border-color: var(--azul-primario);
  font-weight: 600;
  box-shadow: var(--sombra-xs);
}

/* =======================================================
    FORMULARIO
======================================================= */
.devolucion-form {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-bottom: 16px;
  justify-content: center;
}

.devolucion-form input,
.devolucion-form select {
  padding: 8px 12px !important;
  border-radius: var(--radio-full) !important;
  border: 2px solid var(--borde-medio) !important;
  font-size: 0.9rem !important;
  transition: all .25s;
  background: var(--fondo-input) !important;
  color: var(--texto-boton, var(--texto-principal)) !important;
}

.devolucion-form input:focus,
.devolucion-form select:focus {
  border-color: var(--azul-primario);
  box-shadow: 0 0 0 3px rgba(74, 158, 255, 0.15) !important;
  outline: none !important;
  background: var(--fondo-input) !important;
  color: var(--texto-boton, var(--texto-principal)) !important;
}

.devolucion-form button {
  background: linear-gradient(135deg, var(--azul-primario), var(--azul-secundario));
  color: var(--texto-boton, var(--texto-principal));
  border: none;
  border-radius: var(--radio-full);
  padding: 8px 14px;
  cursor: pointer;
  font-size: 0.9rem;
  font-weight: 600;
  transition: all var(--transicion-media);
  box-shadow: var(--sombra-xs);
}

.devolucion-form button:hover {
  box-shadow: var(--sombra-md);
  transform: translateY(-1px);
}

.devolucion-form button:active {
  transform: translateY(0) scale(0.98);
  box-shadow: var(--sombra-xs);
}

.devolucion-form button:active {
  transform: translateY(0) scale(0.95);
}

/* =======================================================
    TABLA PRINCIPAL PREMIUM
======================================================= */
.tabla-devoluciones {
  border-radius: var(--radio-xl);
  overflow: hidden;
  border: 1px solid var(--borde-sutil);
  box-shadow: var(--sombra-xs);
  background: var(--fondo-card);
}

.tabla-devoluciones th {
  background: var(--fondo-secundario);
  padding: 12px 10px;
  font-weight: 600;
  color: var(--texto-principal);
  border-bottom: 2px solid var(--borde-medio);
  font-size: 0.9rem;
}

.tabla-devoluciones td {
  padding: 10px;
  border-bottom: 1px solid var(--borde-sutil);
  background: var(--fondo-card);
  color: var(--texto-principal);
  font-size: 0.9rem;
}

.tabla-devoluciones tbody tr:hover {
  background: var(--fondo-secundario);
  box-shadow: var(--sombra-xs);
}

.tabla-devoluciones input,
.tabla-devoluciones select {
  background: var(--fondo-input);
  border: 1px solid var(--borde-medio);
  color: var(--texto-principal);
  padding: 6px 8px;
  border-radius: var(--radio-full);
  font-size: 0.9rem;
  transition: all var(--transicion-media);
}

.tabla-devoluciones input:focus,
.tabla-devoluciones select:focus {
  outline: none;
  border-color: var(--azul-primario);
  box-shadow: 0 0 0 3px rgba(74, 158, 255, 0.15);
}

/* Botn borrar - Estandarizado al mismo tamao que btn-green y btn-activacion */
.btn-borrar {
  background: linear-gradient(135deg, var(--error), #dc2626) !important;
  color: var(--texto-boton, var(--texto-principal)) !important;
  border: 1px solid var(--borde-sutil) !important;
  border-radius: var(--radio-xl) !important;
  padding: 9px 14px !important;
  cursor: pointer !important;
  font-weight: 600 !important;
  font-size: 0.9rem !important;
  transition: all var(--transicion-media) !important;
  box-shadow: var(--sombra-xs) !important;
  display: inline-flex !important;
  align-items: center !important;
  justify-content: center !important;
  gap: 6px !important;
  height: 36px !important;
  min-width: 36px !important;
  max-width: none !important;
  width: auto !important;
  line-height: 1 !important;
  box-sizing: border-box !important;
  flex-shrink: 0 !important;
}

.btn-borrar:hover:not(:disabled) {
  box-shadow: var(--sombra-md) !important;
  transform: translateY(-1px) !important;
}

.btn-borrar:active:not(:disabled) {
  transform: translateY(0) scale(0.98) !important;
  box-shadow: var(--sombra-xs) !important;
}

.btn-borrar:disabled {
  opacity: 0.5 !important;
  cursor: not-allowed !important;
}

/* Botn naranja (warning) - Estandarizado al mismo tamao que btn-green y btn-activacion */
.btn-orange {
  background: linear-gradient(135deg, var(--warning), #f59e0b) !important;
  color: var(--texto-boton, var(--texto-principal)) !important;
  border: 1px solid var(--borde-sutil) !important;
  border-radius: var(--radio-xl) !important;
  padding: 9px 14px !important;
  cursor: pointer !important;
  font-weight: 600 !important;
  font-size: 0.9rem !important;
  transition: all var(--transicion-media) !important;
  box-shadow: var(--sombra-xs) !important;
  display: inline-flex !important;
  align-items: center !important;
  justify-content: center !important;
  gap: 6px !important;
  height: 36px !important;
  min-width: 36px !important;
  max-width: none !important;
  width: auto !important;
  line-height: 1 !important;
  box-sizing: border-box !important;
  flex-shrink: 0 !important;
}

.btn-orange:hover:not(:disabled) {
  box-shadow: var(--sombra-md) !important;
  transform: translateY(-1px) !important;
}

.btn-orange:active:not(:disabled) {
  transform: translateY(0) scale(0.98) !important;
  box-shadow: var(--sombra-xs) !important;
}

.btn-orange:disabled {
  opacity: 0.5 !important;
  cursor: not-allowed !important;
}

.btn-borrar:hover {
  box-shadow: var(--sombra-md);
  transform: translateY(-2px);
}

.btn-borrar:active {
  transform: translateY(0) scale(0.95);
}

