/* ------------------------------------------------ */
/* ARREGLO DE Z-INDEX PARA EL MODAL Y SWITCHES */
/* ------------------------------------------------ */

.modal-overlay {
    position: fixed !important;
    top: 0 !important;
    left: 0 !important;
    right: 0 !important;
    bottom: 0 !important;
    width: 100% !important;
    height: 100% !important;
    background: rgba(0,0,0,0.6) !important; 
    /* El Z-index ms importante: Debe ser mayor que cualquier elemento de la pgina. */
    z-index: 9999 !important; 
    display: flex !important;
    justify-content: center !important;
    align-items: center !important;
    padding: 20px !important;
    overflow-y: auto !important;
}

.modal-content-activacion {
    background: var(--fondo-card); 
    width: calc(100% - 40px);
    max-width: 600px;
    max-height: calc(100vh - 40px);
    border-radius: var(--radio-xl); 
    padding: 20px;
    box-shadow: var(--sombra-xl);
    border: 1px solid var(--borde-sutil);
    /* Aseguramos que el contenido del modal est sobre el overlay (z-index: 9999) */
    z-index: 10000; 
    color: var(--texto-principal);
    overflow-y: auto;
    overflow-x: hidden;
    margin: auto;
    display: flex;
    flex-direction: column;
}

/* Sistema de tamaos proporcionales para modales de Devoluciones */
.modal {
  background: var(--fondo-card);
  border-radius: 12px;
  box-shadow: var(--sombra-xl);
  border: 1px solid var(--borde-sutil);
  color: var(--texto-principal);
  display: flex;
  flex-direction: column;
  max-height: 90vh;
  overflow: hidden;
  margin: auto;
  position: relative;
}

.modal.modal-sm {
  max-width: 400px;
  width: calc(100% - 40px);
}

.modal.modal-md {
  max-width: 500px;
  width: calc(100% - 40px);
}

.modal.modal-lg {
  max-width: 700px;
  width: calc(100% - 40px);
}

.modal:not(.modal-sm):not(.modal-md):not(.modal-lg):not(.modal-xl) {
  max-width: 500px;
  width: calc(100% - 40px);
}

/* Responsive para mviles */
@media (max-width: 768px) {
  .modal-overlay,
  .activation-modal-overlay {
    padding: 10px !important;
  }

  .modal {
    max-width: calc(100% - 20px) !important;
    width: calc(100% - 20px) !important;
    max-height: calc(100vh - 20px) !important;
  }

  .modal.modal-sm,
  .modal.modal-md,
  .modal.modal-lg {
    max-width: calc(100% - 20px) !important;
    width: calc(100% - 20px) !important;
  }

  .modal-nuevocodigo {
    width: calc(100% - 20px) !important;
    max-width: calc(100% - 20px) !important;
    padding: 16px !important;
  }

  .modal-nuevocodigo h3 {
    font-size: 1.1rem !important;
    margin-bottom: 8px !important;
  }

  .activation-modal {
    width: calc(100% - 20px) !important;
    max-width: calc(100% - 20px) !important;
    padding: 10px !important;
  }

  .modal-content-activacion {
    width: calc(100% - 20px) !important;
    max-width: calc(100% - 20px) !important;
    padding: 16px !important;
  }

  .modal-header {
    padding: 12px 16px !important;
  }

  .modal-header h3 {
    font-size: 1.1rem !important;
  }

  .modal-body {
    padding: 16px !important;
  }

  .modal-body label {
    font-size: 0.85rem !important;
    margin-bottom: 4px !important;
  }

  .modal-body input,
  .modal-body textarea,
  .modal-body select {
    padding: 8px 12px !important;
    font-size: 0.9rem !important;
    margin-bottom: 12px !important;
  }

  .modal-actions,
  .modal-footer {
    padding: 12px 16px !important;
    gap: 8px !important;
    flex-wrap: wrap;
  }

  .modal-actions .btn,
  .modal-actions button,
  .modal-btn-cancel,
  .modal-btn-confirm {
    padding: 10px 16px !important;
    font-size: 0.9rem !important;
    flex: 1;
    min-width: 0;
  }

  .modal-close {
    width: 28px !important;
    height: 28px !important;
    font-size: 1.5rem !important;
  }
}

